import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { loadModules } from 'esri-loader';
import { NgxSpinnerService } from 'ngx-spinner';

import { SharedService } from '../services/shared.service';
import { MapService } from '../services/map.service'

@Component({
  selector: 'coc-map',
  templateUrl: './map.component.html'
})
export class MapComponent implements OnInit {
  
  public map: __esri.Map;
  public mapView: __esri.MapView;
  public clusterLayer: any;
  panRequestSubscrition: any;
  businessResultsSubscription: any;
  selectedBusinessSubscription: any;
  mapStatusSubscription: any;
  searchCoordinatesSubscription: any;
  private mapLoaded: boolean;
  private clicked: boolean = false;
  public businessResults: any[] = [];
  public searchResults: any[] = [];
  public selectedBusiness: any = {
    name: '',
    address: '',
    phone: '',
    website: '',
    openedOn: '',
    parentCategory: '',
    icon: '',
    isActive: false
  };

  options = {
    dojoConfig: {
      async: true,
      tlmSiblingOfDojo: false,
      packages: [{
        location: location.pathname.replace(/\/[^/]+$/, '') + "assets/fcl",
        name: 'fcl'
      }],
      has: {
        "esri-promise-compatibility": 1
      }
    }
  }

  @ViewChild('map') private mapViewEl: ElementRef;

  closeCard = (result) => {
    result.isActive = !result.isActive
  }

  constructor(
    public sharedService: SharedService,
    private mapService: MapService,
    private spinner: NgxSpinnerService
  ) { }

  ngOnInit() {

    this.mapService.currentResults$.subscribe(
      businessResults => {
        this.businessResults = businessResults;
      }
    )

    this.mapService.mapStatus$.subscribe(
      mapLoaded => {
        this.mapLoaded = mapLoaded;
      }
    )

    this.mapService.currentBusiness$.subscribe(
      selectedBusiness => {
        this.selectedBusiness = selectedBusiness;
      }
    )

    this.mapService.getCoordinates$.subscribe(
      searchResults => {
        this.searchResults = searchResults;
      }
    )

    this.spinner.show();

    // use esri-loader to load JSAPI modules  
    return loadModules([
      'esri/Map',
      'esri/Color',
      'esri/views/MapView',
      'esri/views/SceneView',
      'esri/views/ui/DefaultUI',
      'esri/geometry/Extent',
      'esri/geometry/SpatialReference',
      'esri/geometry/Point',
      'esri/layers/GraphicsLayer',
      'esri/Graphic',
      'esri/symbols/SimpleMarkerSymbol',
      'esri/symbols/SimpleLineSymbol',
      'esri/symbols/SimpleFillSymbol',
      'esri/symbols/TextSymbol',
      'esri/symbols/TextSymbol3DLayer',
      'esri/symbols/Font',
      'esri/renderers/ClassBreaksRenderer',
      'esri/core/watchUtils',
      'esri/geometry/support/webMercatorUtils',
      'fcl/FlareClusterLayer_v4',
      'dojo',
      'dojo/ready',
      'dojo/json',
      'dojo/dom',
      'dojo/on',
      'dojo/dom-attr',
      'dojo/dom-class'
    ], this.options)
      .then(([Map, Color, MapView, SceneView, Extent, SpatialReference, Point, PopupTemplate, GraphicsLayer, Graphic, SimpleMarkerSymbol, SimpleLineSymbol, SimpleFillSymbol, TextSymbol, TextSymbol3DLayer, Font, ClassBreaksRenderer,
        watchUtils, webMercatorUtils, fcl, dojo, ready, JSON, dom, on, domAttr, domClass ]) => {
        
        this.map = new Map({
          basemap: 'streets'
        });

        this.mapView = new MapView({
          container: this.mapViewEl.nativeElement,
          center: [-111.8413725, 33.3014505],
          zoom: 15,
          map: this.map,
        })

        this.mapView.ui.move('zoom', 'bottom-right')

        const graphicsLayer: __esri.GraphicsLayer = new GraphicsLayer({})

        this.map.add(graphicsLayer);

        this.mapView.when(
          () => {

            this.spinner.hide();

            this.mapStatusSubscription = this.mapService.mapLoaded.subscribe(() => {
              /* 
               * Notify search box that map has loaded and enable search button
              */
            })

            let activePin = 'M172.268 501.67C26.97 291.031 0 269.413 0 192 0 85.961 85.961 0 192 0s192 85.961 192 192c0 77.413-26.97 99.031-172.268 309.67-9.535 13.774-29.93 13.773-39.464 0zM192 272c44.183 0 80-35.817 80-80s-35.817-80-80-80-80 35.817-80 80 35.817 80 80 80z'

            this.panRequestSubscrition = this.mapService.panRequest.subscribe(() => {
              panMap(this.mapView, this.mapService.businessCoordinates);
            });

            this.businessResultsSubscription = this.mapService.currentResults$.subscribe(() => {
              addGraphics(this.map, this.clusterLayer, this.businessResults);
            });

            this.selectedBusinessSubscription = this.mapService.currentBusiness$.subscribe(() => {
              toggleActiveBusiness(this.mapView, this.businessResults, this.selectedBusiness);
            });

            this.searchCoordinatesSubscription = this.mapService.getCoordinates$.subscribe(() => {
              recenterMap(this.mapView, this.searchResults);
            })
      
            function panMap(mapView, coordinates) {
              mapView.goTo({
                target: coordinates,
                //zoom: 18
              }, { /* duration: 1500 */ }).then(() => {
                const mapPin: __esri.Graphic = new Graphic({
                  attributes: {
                    name: 'mapPin'
                  },
                  geometry: {
                    type: 'point',
                    longitude: coordinates[0],
                    latitude: coordinates[1]
                  },
                  symbol: {
                    type: 'simple-marker',
                    color: '#003c5c',
                    path: activePin,
                    size: '30px'
                  }
                })
                if(graphicsLayer.graphics.length) {
                  graphicsLayer.graphics.removeAt(graphicsLayer.graphics.length - 1)
                } 
                graphicsLayer.graphics.add(mapPin);
              }) 
            }

            function addGraphics(map, clusterLayer, businessResults) {
              map.remove(clusterLayer);
              let defaultSym: __esri.SimpleMarkerSymbol = new SimpleMarkerSymbol ({
                size: '30px',
                color: '#003c5c',
                path: 'M192 96c-52.935 0-96 43.065-96 96s43.065 96 96 96 96-43.065 96-96-43.065-96-96-96zm0 160c-35.29 0-64-28.71-64-64s28.71-64 64-64 64 28.71 64 64-28.71 64-64 64zm0-256C85.961 0 0 85.961 0 192c0 77.413 26.97 99.031 172.268 309.67 9.534 13.772 29.929 13.774 39.465 0C357.03 291.031 384 269.413 384 192 384 85.961 298.039 0 192 0zm0 473.931C52.705 272.488 32 256.494 32 192c0-42.738 16.643-82.917 46.863-113.137S149.262 32 192 32s82.917 16.643 113.137 46.863S352 149.262 352 192c0 64.49-20.692 80.47-160 281.931z',
                outline: null
              })
      
              let renderer: __esri.ClassBreaksRenderer = new ClassBreaksRenderer({
                field: 'clusterCount',
                defaultSymbol: defaultSym
              })
      
              var smSymbol: __esri.SimpleMarkerSymbol = new SimpleMarkerSymbol({ size: 22, outline: new SimpleLineSymbol({ color: [221, 159, 34] }), color: [255, 204, 102] });
              var mdSymbol: __esri.SimpleMarkerSymbol = new SimpleMarkerSymbol({ size: 24, outline: new SimpleLineSymbol({ color: [82, 163, 204] }), color: [102, 204, 255] });
              var lgSymbol: __esri.SimpleMarkerSymbol = new SimpleMarkerSymbol({ size: 28, outline: new SimpleLineSymbol({ color: [41, 163, 41] }), color: [51, 204, 51] });
              var xlSymbol: __esri.SimpleMarkerSymbol = new SimpleMarkerSymbol({ size: 32, outline: new SimpleLineSymbol({ color: [200, 52, 59] }), color: [250, 65, 74] });
      
              renderer.addClassBreakInfo(0, 19, smSymbol);
              renderer.addClassBreakInfo(20, 150, mdSymbol);
              renderer.addClassBreakInfo(151, 1000, lgSymbol);
              renderer.addClassBreakInfo(1001, Infinity, xlSymbol);
              
              var options = {
                id: "flare-cluster-layer",
                clusterRenderer: renderer,
                displaySubTypeFlares: false,
                maxSingleFlareCount: 8,
                clusterRatio: 75,
                data: businessResults
              }
      
              clusterLayer = new fcl.FlareClusterLayer(options);
              map.add(clusterLayer);
            }

            function toggleActiveBusiness(mapView, businessResults, selectedBusiness) {
              if(selectedBusiness.isActive) {
                return;
              }
              //Drop A Pin At The Coordinates
              let coordinates = [selectedBusiness.x, selectedBusiness.y]
              mapView.goTo({
                target: coordinates,
                //zoom: 18
              }, { /* duration: 1500 */ }).then(() => {
                const mapPin: __esri.Graphic = new Graphic({
                  attributes: {
                    isActive: selectedBusiness.isActive
                  },
                  geometry: {
                    type: 'point',
                    longitude: coordinates[0],
                    latitude: coordinates[1]
                  },
                  symbol: {
                    type: 'simple-marker',
                    color: '#003c5c',
                    path: activePin,
                    size: '30px'
                  }
                })
                if(graphicsLayer.graphics.length) {
                  graphicsLayer.graphics.removeAt(graphicsLayer.graphics.length - 1)
                } 
                graphicsLayer.graphics.add(mapPin);
              })
              //Loop through all businesses and set active state to false
              businessResults.map((result) => {
                return result.isActive = false;
              })
              //Set selected business state to active
              selectedBusiness.isActive = true;
              document.getElementById('br-' + selectedBusiness.id).scrollIntoView({ behavior: "smooth", block: "end"})
            }

            function recenterMap(mapView, searchResults) {
              let coordinates = [searchResults.candidates[0].location.x, searchResults.candidates[0].location.y];
              mapView.goTo({
                target: coordinates
              }, { duration: 1500 }).then(() => {
                //console.log("Map recentered");
              })
            }

            this.mapView.on('click', (event: __esri.MapViewClickEvent) => {
              this.mapView.hitTest(event).then((response) => {
                if (response.results.length) {
                  let graphic = response.results[0].graphic;
                  this.mapService.setCurrentBusiness(graphic.attributes);
                }
              })
            })
          },
          (err) => {
            console.log(err);
          }
        );
      });
  }
}
