  getAddress(address: string): Observable<any> {
    const url = `${this.addressUrl}/findAddressCandidates?Street=&ZIP=&Neighborhood=&City=&Subregion=&State=&Country=&SingleLine=${address}&category=&outFields=&maxLocations=&outSR=4326&searchExtent=&location=&distance=&magicKey=&f=pjson`;
    return this.http.get<any>(url)
      .pipe(
        //tap(res => console.log(res.candidates)),
        catchError(this.handleError<any>('getAddress', []))
      )
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  panToBusiness(business_coordinates) {
    this.businessCoordinates = business_coordinates;
    this.panRequest.next();
  }

