import { Component, OnInit } from '@angular/core';
import { Observable, Subscription } from 'rxjs';

import { SharedService } from '../services/shared.service';
import { MapService } from '../services/map.service';

@Component({
  selector: 'coc-main-list-view',
  templateUrl: './main-list-view.component.html'
})
export class MainListViewComponent implements OnInit {
  subscription: Subscription;
  isMapView: boolean;
  businessResults: any[] = [];

  constructor(
    public sharedService: SharedService, 
    public mapService: MapService) { 
    
  }

  ngOnInit() {
    this.sharedService.getCurrentView$.subscribe(
      isMapView => {
        this.isMapView = isMapView;
      }
    )
    
    this.mapService.currentResults$.subscribe(
      businessesResults => {
        this.businessResults = businessesResults
      }
    )
  }

}
