import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner'

import { SharedService } from '../services/shared.service';
import { MapService } from '../services/map.service';
import { Location } from '../models/location'
import { setTime } from 'ngx-bootstrap/chronos/utils/date-setters';

@Component({
  selector: 'coc-search-box',
  templateUrl: './search-box.component.html'
})
export class SearchBoxComponent implements OnInit {
  
  isListView: boolean;
  isMapView: boolean;
  businessResults = this.mapService.data;
  // Business Categories SubCategories
  administrative = this.mapService.administrativeSubCategories;
  arts = this.mapService.artsSubCategories;
  construction = this.mapService.constructionSubCategories;
  education = this.mapService.educationSubCategories;
  finance = this.mapService.financeSubCategories;
  health = this.mapService.healthSubCategories;
  hotel = this.mapService.hotelSubCategories;
  manufacturing = this.mapService.manufacturerSubCategories;
  news = this.mapService.newsSubCategories;
  realEstate = this.mapService.realEstateSubCategories;
  restaurant = this.mapService.restaurantSubCategories;
  retail = this.mapService.retailSubCategories;
  services = this.mapService.servicesSubCategories;
  transportation = this.mapService.transportationSubCategories;
  utilities = this.mapService.utilitiesSubCategories;
  wholeSale = this.mapService.wholesalerSubCategories;
  licenseType = this.mapService.licenseType;
  filterOptions: any[] = [];
  licenseFilterButtonText: string = "Show All";
  searchAddress: string = '175 S Arizona Ave, Chandler, AZ 85225';
  validatingAddress: boolean;

  updateFilterOptions(e) {
    if(e.target.checked) {
      this.filterOptions.push(e.target.value);
    } else {
      if(this.filterOptions.includes(e.target.value)) {
        for(let i = 0; i < this.filterOptions.length; i++) {
          if(e.target.value == this.filterOptions[i]) {
            this.filterOptions.splice(i, 1);
          }
        }
      }
    }
  }
  
  updateLicenseFilter(e) {
    this.licenseFilterButtonText = e.target.innerHTML;
  }

  updateSearchAddress(address) {
    this.mapService.setSearchAddress(address);
  }

  checkAll(ev) {
    let value = ev.target.value;
    switch(value) {
      case "administrative":
        this.administrative.map(x => { return x.state = ev.target.checked })
        break;
      case "arts":
        this.arts.map(x => { return x.state = ev.target.checked })
        break;
      case "construction":
        this.construction.map(x => { return x.state = ev.target.checked })
        break;
      case "education":
        this.education.map(x => { return x.state = ev.target.checked })
        break;
      case "finance":
        this.finance.map(x => { return x.state = ev.target.checked })
        break;
      case "health":
        this.health.map(x => { return x.state = ev.target.checked })
        break;
      case "hotel":
        this.hotel.map(x => { return x.state = ev.target.checked })
        break;
      case "manufacturing":
        this.manufacturing.map(x => { return x.state = ev.target.checked })
        break;
      case "news":
        this.news.map(x => { return x.state = ev.target.checked })
        break;
      case "realEstate":
        this.arts.map(x => { return x.state = ev.target.checked })
        break;
      case "restaurant":
        this.restaurant.map(x => { return x.state = ev.target.checked })
        break;
      case "retail":
        this.retail.map(x => { return x.state = ev.target.checked })
        break;
      case "services":
        this.services.map(x => { return x.state = ev.target.checked })
        break;
      case "transportation":
        this.transportation.map(x => { return x.state = ev.target.checked })
        break;
      case "utilities":
        this.utilities.map(x => { return x.state = ev.target.checked })
        break;
      case "wholeSale":
        this.wholeSale.map(x => { return x.state = ev.target.checked })
        break;
      default:
        console.log('Error');
    }
  }

  isAllAdministrativeChecked() { return this.administrative.every(_ => _.state); }
  isAllArtsChecked() { return this.arts.every(_ => _.state); }
  isAllConstructionChecked() { return this.construction.every(_ => _.state); }
  isAllEducationChecked() { return this.education.every(_ => _.state); }
  isAllFinanceChecked() { return this.finance.every(_ => _.state); }
  isAllHealthChecked() { return this.health.every(_ => _.state); }
  isAllHotelChecked() { return this.hotel.every(_ => _.state); }
  isAllManufacturingChecked() { return this.manufacturing.every(_ => _.state); }
  isAllNewsChecked() { return this.news.every(_ => _.state); }
  isAllRestaurantChecked() { return this.restaurant.every(_ => _.state); }
  isAllRetailChecked() { return this.retail.every(_ => _.state); }
  isAllServicesChecked() { return this.services.every(_ => _.state); }
  isAllTransportationChecked() { return this.transportation.every(_ => _.state); }
  isAllUtilitiesChecked() { return this.utilities.every(_ => _.state); }
  isAllWholesaleChecked() { return this.wholeSale.every(_ => _.state); }

  toggleListView() {
    this.sharedService.setView(!this.isListView);
  }

  loadData() {
    let unfilteredResults = this.mapService.data;
    if(this.filterOptions.length) {
      let filteredResults = this.businessResults.filter((result) => {
        for(let i = 0; i < this.filterOptions.length; i++) {
          if(result.subCategory == this.filterOptions[i]) {
            return result;
          } 
        }
      })
      this.mapService.setBusinessResults(filteredResults);
    } else {
      this.mapService.setBusinessResults(unfilteredResults);
    }
    if(this.isMapView) { this.toggleListView(); }
  }

  search = (e) => {
    e.preventDefault();
    this.validatingAddress = true;
    setTimeout(() => {
      this.mapService.getAddress(this.searchAddress).subscribe(
        response => {
          if(response.candidates.length) {
            this.mapService.setSearchCoordinates(response);
            this.loadData();
            this.validatingAddress = false;
          } else {
            alert('Not a valid address. Please try again.');
            this.validatingAddress = false;
          }
        }
      );
    },2000)
  }

  constructor(
    public sharedService: SharedService,
    private mapService: MapService) { 
  }

  ngOnInit() {

    this.sharedService.getView$.subscribe(
      isListView => {
        this.isListView = isListView;
      }
    )

    this.mapService.currentResults$.subscribe(
      businessesResults => {
        this.businessResults = businessesResults;
      }
    )

    this.sharedService.getCurrentView$.subscribe(
      isMapView => {
        this.isMapView = isMapView;
      }
    )

    this.mapService.currentAddress$.subscribe(
      searchAddress => {
        this.searchAddress = searchAddress;
      }
    )

  }
}
